.footer {
    background-color: $color-grey-dark-3;
    margin-top: 5rem;
    padding: 8rem 5rem;
    font-size: 1.4rem;
    color: $color-grey-light-1;
    display: block;
    
    @include respond(tabPort) {
        padding: 8rem 3rem;
    }

    &__logobox {
        text-align: center;
    
        &--logo {
            width: 8rem;
            height: 8rem;
        }
    }

    
    &__data {
        display: table;
        font-size: 2rem;
        line-height: 3rem;
        margin: 0 auto;

        a:active, a:link, a:visited {
            text-decoration: none;
            color: $color-grey-light-1    
        }
    }
}